import React, { Component } from 'react';
import allNaturalImg from '.././assets/icons/allNatural.png';
import awesomeImg from '.././assets/icons/awesome.png';
import chemicalFreeImg from '.././assets/icons/chemicalFree.png';
import fightingAnimalTestingImg from '.././assets/icons/fightingAnimalTesting.png';
import handmadeImg from '.././assets/icons/handmade.png';
import organicImg from '.././assets/icons/organic.png';
import preservativeFreeImg from '.././assets/icons/preservativeFree.png';
import vegetarianImg from '.././assets/icons/vegetarian.png';

const properties = [
  [
    {
        text: "All-Natural",
        icon: allNaturalImg
    },
    {
        text: "Organic",
        icon: organicImg
    },
    {
        text: "Handmade",
        icon: handmadeImg
    },
    {
        text: "Chemical-Free",
        icon: chemicalFreeImg
    }
  ],
  [
    {
        text: "Preservative-Free",
        icon: preservativeFreeImg
    },
    {
        text: "Fighting Animal Testing",
        icon: fightingAnimalTestingImg
    },
    {
        text: "Vegetarian",
        icon: vegetarianImg
    },
    {
        text: "100% Amazing!",
        icon: awesomeImg
    }
    ]
]

export default class LandingPage extends Component {

    render() {
      
        let propertyTopDivs = properties[0].map((property) => {
            return(
              <div className="property" style={styles.property}>
                  <div className="property-img-container"><img styles={{width: "100%", height: "100%"}} src={property.icon} /></div>
                  <div>{property.text}</div>
              </div>
            )
        })
        
        let propertyBottomDivs = properties[1].map((property) => {
            return(
              <div className="property" style={styles.property}>
                  <div className="property-img-container"><img styles={{width: "100%", height: "100%"}} src={property.icon} /></div>
                  <div>{property.text}</div>
              </div>
            )
        })
      
        return(
            <div>
                <div style={styles.landingContainer}>
                  <div className="landing-msg">All products are...</div>
                  <div style={styles.properties}>
                      <div className="properties-top">{propertyTopDivs}</div>
                      <div className="properties-bottom">{propertyBottomDivs}</div>
                  </div>
                </div>
            </div>
        )
    }
    
}

const styles = {
    landingHeader: {
        height: 200,
        backgroundColor: "black"
    },
    landingContainer: {
        margin: 10
    },
    message: {
      margin: "30px 5%"
    },
    properties: {
    },
    property: {
      display: "inline-block"
    }
}


