import React, { Component } from 'react';

export default class DetailModal extends Component {

    render() {
      console.log(this.props.img);
        return(
            <div className="modal-container" style={this.props.open ? {opacity: 1} : {opacity: 0, pointerEvents: "none"}}>
                <div className="modal">
                    <h2 className="modal-heading">{this.props.modalHeading}</h2>
                    <div className="modal-content">
                      <div className="modal-message">{this.props.modalMessage}</div> 
                      <div className="modal-img" style={{width: "50%", paddingBottom: "20%", transition: "all .8s", backgroundImage: `url(${this.props.img}`, backgroundSize: "100% 100%"}} />
                      <div className="modal-btn-container">
                        <button style={{...styles.button, ...styles.buttonPrimary}} onClick={this.btnClick.bind(this)} className="modal-btn">{this.props.modalBtnText}</button>
                      </div>
                    </div>
                    <div style={{paddingBottom: 1}}></div>
                </div>
            </div>
        )
    }
    
    btnClick() {
        this.props.click(null)
    }
    
}

const styles = {
  button: {
      width: 200,
      height: 50,
      border: "1px solid #24C6DC",
      borderRadius: 100,
      boxShadow: "0 6px 10px 0 rgba(0,0,0,0.18)",
      cursor: "pointer"
  },
  buttonPrimary: {
      color: "#FFFFFF",
      background: "#24C6DC",
      transition: "all .8s"
  },
}


